@tailwind base;
@tailwind components;
@tailwind utilities;
@import url('https://fonts.googleapis.com/css2?family=Cormorant+Upright:wght@300;400;500;600;700&family=Open+Sans:wght@300;400;500;600;700&display=swap');
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
}
:root {
  --font-base: 'Cormorant Upright', serif;
  --font-alt: 'Open Sans', sans-serif;
  
  --color-golden: #DCCA87;
  --color-black: #0C0C0C;
  --color-gray: #545454;
  --color-crimson: #F5EFDB;
  --color-grey: #AAAAAA;
  --color-white: #FFFFFF;
}
.darkMode{
  --color-golden2: #dbc577;
  --bg_black: #171717;
  --text: rgb(232, 229, 229);
  --light_mode: #f7f6c1;
  --button_text: #ada9a9;
  --text_hover: #abbdfa;
  --background: #383636;
  --background2: #2e2d2d;
  --blue: rgb(116, 116, 242);
  --dark: rgb(31, 30, 30);
  --star: rgb(255, 255, 5);
  transition: background-color 0.25s ease-in-out;
}
.lightMode{
  --star: rgb(0, 98, 255);
  --color-golden2: #cb9f01;
  --bg_black: #f0efef;
  --text: rgb(0, 0, 0);
  --light_mode: #f7f6c1;
  --button_text: #5f5d5d;
  --text_hover: #0e2985;
  --background: #e7e4e4;
  --background2:rgb(208, 207, 207);
  --blue: rgb(255, 98, 98);  
  --dark: rgb(31, 30, 30);
  transition: background-color 0.25s ease-in-out;
}
body {
  font-family: "Poppins", sans-serif;
  min-height: 100vh;
  background-color: var(--background);
  overflow-x: hidden;
}
#mainDiv {
  font-family: "Poppins", sans-serif;
  min-height: 100vh;
  background-color: var(--background);
}