.section__padding {
    padding: 4rem 6rem;
    margin: 80px 0;
    width: 100%;
    background-color: var(--background2);
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  }
  
  .flex__center {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .app__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
  
    min-height: 100vh;
  }
  .app__wrapper_info {
    flex: 1;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    position: relative;
  }
  .app__wrapper_info #stars{
    position: absolute;
    font-size: 30px;
    top: 0;
    right: 7rem;
    color: var(--star);
    animation: stars 2s infinite;
    transition: all ease;
  }
  @keyframes stars {
    50%{
      transform: scale(1.15);
    }
  }
  .app__wrapper_img {
    flex: 1;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 2rem;
    position: relative;
  }
  line{
    position: absolute;
}
#line1{
    background-color: var(--background);
    height: 50px;
    width: 250px;
    position: absolute;
    top: -25px;
    right: 15px;
    z-index: 5;
}
#line2{
    background-color: var(--background);
    height: 250px;
    width: 50px;
    position: absolute;
    top: -25px;
    right: 15px;
    z-index: 5;
}
#line3{
    background-color: var(--background);
    height: 50px;
    width: 250px;
    position: absolute;
    bottom: -25px;
    left: 15px;
    z-index: 5;
}
#line4{
    background-color: var(--background);
    height: 250px;
    width: 50px;
    position: absolute;
    bottom: -25px;
    left: -30px;
    z-index: 5;
}


  .app__wrapper_img-reverse {
    justify-content: flex-start;
    margin-right: 5rem;
  }
  .app__wrapper_img-reverse img{
    width: 560px;
    border-top-left-radius: 20px;
    border-bottom-right-radius: 20px;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
    z-index: 10;
  }
  .p__cormorant {
    font-family: var(--font-base);
    color: var(--text);
    font-weight: 700;
    letter-spacing: 0.04em;
    text-transform: capitalize;
    font-feature-settings: 'tnum' on, 'lnum' on;
    line-height: 29.9px;
    font-size: 23px;
  }
  
  .p__opensans {
    font-family: var(--font-alt);
    color: var(--text);
    font-weight: 400;
    letter-spacing: 0.04em;
    text-transform: capitalize;
    line-height: 28px;
    font-size: 16px;
  }
  
  .headtext__cormorant {
    font-family: var(--font-base);
    color: var(--color-golden2);
    font-size: 64px;
    line-height: 83.2px;
    letter-spacing: 0.04em;
    text-transform: capitalize;
  }
  .app__jyoti-content {
    display: flex;
    flex-direction: column;
    width: 100%;
    margin-top: 5rem;
}

.app__jyoti-content_quote {
    display: flex;
    justify-content: flex-start;
    align-items: flex-end;
}

.app__jyoti-content_quote img {
    width: 47px;
    height: 40px;
    margin: 0 1rem 1rem 0;
}

.app__jyoti-sign {
    width: 100%;
    margin-top: 3rem;
}

.app__jyoti-sign p:first-child {
    font-family: var(--font-base);
    font-weight: 400;
    font-size: 32px;
    line-height: 41.6px;
    letter-spacing: 0.04em;
    text-transform: capitalize;
    color: var(--color-golden2);
}

.app__jyoti-sign img {
    width: 250px;
    margin-top: 2rem;
}

@media screen and (min-width: 2000px) {
    .app__jyoti-sign img {
        width: 370px;
    }
}



@media screen and (max-width: 450px) {
  .section__padding {
    width: 100vw;
    padding: 1rem 2rem;
    margin: 10px 0;
    display: flex;
    flex-direction: column;
    margin-bottom: 3rem;
  }
  .app__wrapper_info #stars{
    font-size: 20px;
    top: 12%;
    right: 10%;
  }
  .app__wrapper_info {
    /* border: 2px solid yellow; */
    width: 100vw;
    text-align: center;
    align-items: center;
    flex-direction: column;
    display: flex;
  }
  #line1,#line2,#line3,#line4{
    display: none;
  }
  .app__wrapper_img-reverse {
    justify-content: flex-start;
    margin-right: 5rem;
    /* border: 2px solid yellow; */
    margin: 0 auto;
  }
  .p__cormorant {
    line-height: 24.9px;
    font-size: 20px;
  }
  .p__opensans {
    line-height: 18px;
    font-size: 14px;
    text-align: center;
    max-width: 90%;
    margin: 0 auto;
  }
  .headtext__cormorant {
    /* border: 2px solid blueviolet; */
    font-size: 34px;
    width: 100vw;
    line-height: 40.2px;
    margin: 0 auto;
    margin-bottom: 1rem;
  }
  .app__jyoti-content {
    margin-top: 2rem;
    margin: 0 auto;
    width: 90%;
    gap: 10px;
    /* border: 2px solid blueviolet; */
  }
  .app__jyoti-content_quote img {
    width: 27px;
    height: 20px;
  }
  .app__jyoti-sign p:first-child {
    font-size: 27px;
    line-height: 30.6px;
  }
  .app__jyoti-sign img {
    width: 120px;
    margin-top: 1rem;
    margin: 0 auto;
    margin-top: 1rem;
  }

}