.app__footer {
    width: 100%;
    position: relative;
    z-index: 1;
    padding: 1rem 6rem;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
    background: var(--background2);
    padding-top: 0;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}

.app__footer-links {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;
    margin-top: 2rem;
    padding: 0 2rem;
}

.app__footer-links_contact,
.app__footer-links_logo{
    flex: 1;
    margin: 1rem;
    text-align: center;
}
.p__opensans{
    font-family: var(--font-alt);
    color: var(--text);
    font-weight: 400;
    letter-spacing: 0.04em;
    text-transform: capitalize;
    line-height: 28px;
    font-size: 16px;
}
.myQuote{
    font-family: var(--font-base);
}
.app__footer-links_icons{
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 1rem auto;
    gap: 2rem;
}
.app__footer-links_icons svg {
    color: var(--text);
    margin: 0.5rem;
    font-size: 30px;
    cursor: pointer;
    transition: 0.5s ease;
}

.app__footer-links_icons svg:hover {
    color: var(--blue);
    scale: 1.1;
}

.app__footer-headtext {
    font-family: var(--font-base);
    color: var(--text_hover);
    font-weight: 400;
    letter-spacing: 0.04em;
    text-transform: capitalize;
    font-size: 32px;
    line-height: 41.6px;
    margin-bottom: 1rem;
}
.footer__copyright {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 1rem;
    margin-top: 1rem;
}
.footer__copyright p span{
    color: var(--blue);
    font-size: large;
}
#linkedin{
    font-size: x-large;
    color: var(--text);
    transition: 0.5s ease;
}
#linkedin:hover{
    color: rgb(60, 88, 180);
    scale: 1.08;
}

@media screen and (min-width: 2000px) {
    .app__footer-headtext {
        font-size: 51px;
        line-height: 61.6px;
    }
}

@media screen and (max-width: 1150px) {
    .app__footer-links {
        align-items: center;
        flex-direction: column;
        padding: 0;
    }

    .app__footer-links_contact,
    .app__footer-links_logo,
    .app__footer-links_work {
        margin: 2rem 0;
        width: 100%;
    }
}

@media screen and (max-width: 650px) {
    .app__footer {
        padding: 0 0 2rem 0;
    }
}

@media screen and (max-width: 350px) {
    .app__footer-links_logo img:first-child {
        width: 80%;
    }
}