.yt-gallery{
    flex-direction: row;
    background: var(--background2);
    padding: 4rem 6rem 4rem 0;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 100px 0;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}
.yt-gallery-content{
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    min-width: 400px;
    padding-right: 1rem;
    margin-left: 190px;
}
.yt-gallery-content button{
    margin-top: 1rem;
}
.yt-image{
    flex: 1;
    display: flex;
    flex-direction: row;
    max-width: 55%;
    position: relative;
}
.yt-image_container{
    display: flex;
    flex-direction: row;
    width: max-content;
    overflow-x: scroll;
    -ms-overflow-style: none;
    scrollbar-width: none;  
}
.yt-image_container::-webkit-scrollbar {
    display: none;
}
.yt-card{
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    min-width: 301px;
    height: 447px;
    margin-right: 2rem;
    box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
    border-radius: 15px;
}
.yt-image-icon{
    position: absolute;
    color: #fff;
    font-size: 2rem;
    opacity: 0;
    transition: .5s ease;
    cursor: pointer;
}
.yt-card img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    opacity: 1;
    transition: .5s ease;
}
.yt-card:hover img {
    opacity: .35;
}
.yt-card:hover .yt-image-icon {
    opacity: 1;
    font-size: xx-large;
}
.yt-arrows{
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 1rem;
    position: absolute;
    bottom: 5%;
}
.yt-arrow-icon {
    color: var(--color-golden);
    font-size: 2rem;
    cursor: pointer;
    background: var(--color-black);
    border-radius: 5px;
}
.yt-arrow-icon:hover {
    color: var(--color-white);
}
.custom__button{
      background-color: var(--bg_black) !important; 
      color: var(--button_text);
      font-family: var(--font-base);
      font-weight: 700;
      letter-spacing: 0.04em;
      line-height: 28px;
      font-size: 24px;
      padding: 0.5rem 1.5rem;
      margin: 1rem 0;
      border-radius: 15px;
      border: none;
      outline: none;
      cursor: pointer;
      transition: 0.5s ease;
      box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
}
.custom__button:hover{
    background-color: var(--background2);
    scale: 1.035;
    color: var(--text);
}
#yt-para{
        font-family: var(--font-base);
        color: var(--text);
        font-weight: 400;
        letter-spacing: 0.04em;
        text-transform: capitalize;
        line-height: 28px;
        font-size: 16px;
}
.headText {
    font-family: var(--font-base);
    color: var(--color-golden2);
    font-size: 64px;
    line-height: 83.2px;
    letter-spacing: 0.04em;
    text-transform: capitalize;
}
.subHeading{
    margin-bottom: 1rem;
    font-family: var(--font-base);
    color: var(--text);
    font-weight: 700;
    letter-spacing: 0.04em;
    text-transform: capitalize;
    font-feature-settings: 'tnum' on, 'lnum' on;
    line-height: 29.9px;
    font-size: 28px;
    opacity: 0.75;   
}


@media screen and (max-width: 450px) {
    .yt-gallery{
        width: 100vw;
        display: flex;
        flex-direction: column;
        padding: 2rem 0 1rem 1rem;
    }
    .yt-gallery-content{
        padding-top: 2rem;
        margin: 0 auto;
        align-items: center;
    }
    .yt-image{
        max-width: 100%;
    }
    .yt-card{
        min-width: 200px;
        height: 300px;
        margin-right: 1rem;
    }
    #yt-para{
        margin-top: 1rem;
        max-width: 100%;
        margin: 0 auto;
        text-align: center;
    }
    .headText {
        font-size: 38px;
        line-height: 53.2px;
    }
    .subHeading{
        line-height: 19.9px;
        font-size: 28px;
        opacity: 0.75;   
    }
}