.signup-form{
    width: 100%;
    font-family:  var(--font-alt);
    background-repeat: no-repeat;
    position: relative;
    background-size: cover;
    background: url('../../assets/bg/sketch.jpeg');
    background-repeat: no-repeat;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 90vh;
    background-size: cover;
}
#circle{
    position: absolute;
    width: 200px;
    height: 200px;
    background-color: rgb(178, 195, 227);
    border-radius: 50%;
    opacity: 0.5;
    top: 100px;
    left: 400px;
    animation: circleMove 5s  infinite;
    box-shadow: rgba(0, 0, 0, 0.15) 0px 14px 28px, rgba(0, 0, 0, 0.12) 0px 10px 10px;    
}
@keyframes circleMove {
    50%{
        transform: translate(20px,0px);
    }
}
.signup-wrapper{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 25px 0;
    width: 600px;
    border-radius: 15px;
}
.signup-input{
    display: flex;
    justify-content:center;
    padding: 15px 15px;
    gap: 15px;
    align-items: center;
}

#signup-heading{
    font-size: 50px;
    font-weight: 600;
    font-family: var(--font-base);
    color: black;
    opacity: 0.7;
}
.signup-intro p{
    color: black;
    opacity: 0.65;
    font-family: var(--font-base);
    font-size: 20px;
    font-weight: 600;
    padding-left: 20px;
}
input[type=text], select {
    width: 100%;
    padding: 5px 20px;
    margin: 8px 0;
    display: inline-block;
    border: 1px solid #ccc;
    border-radius: 10px;
    box-sizing: border-box;
    outline: none;
    border: none;
    opacity: 0.6;
    box-shadow: rgba(0, 0, 0, 0.55) 1.95px 1.95px 2.6px;
}
input[type=password], select {
    width: 100%;
    padding: 5px 20px;
    margin: 8px 0;
    display: inline-block;
    border: 1px solid #ccc;
    border-radius: 10px;
    box-sizing: border-box;
    outline: none;
    border: none;
    opacity: 0.6;
    box-shadow: rgba(0, 0, 0, 0.55) 1.95px 1.95px 2.6px;
}
input[type=email], select {
    width: 100%;
    padding: 5px 20px;
    margin: 8px 0;
    display: inline-block;
    border: 1px solid #ccc;
    border-radius: 10px;
    box-sizing: border-box;
    outline: none;
    border: none;
    opacity: 0.6;
    box-shadow: rgba(0, 0, 0, 0.55) 1.95px 1.95px 2.6px;
}
#signup-button{
    font-size: 20px;
    padding: 3px 15px;
    width: 150px;
    margin-top: 38px;
    border-radius: 15px;
    box-shadow: rgba(0, 0, 0, 0.5) 0px 5px 15px;
    color: rgb(68, 67, 67);
    font-weight: 600;
    letter-spacing: 1px;
    transition: 1s ease;
}
#signup-button:hover{
    background-color: rgb(82, 157, 82);
    color: white;
    cursor: pointer;
}
form{
    margin: 0 auto;
    margin-top: 30px;
    margin-bottom: 55px;
    backdrop-filter: blur(4px);
    box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 20px, rgba(0, 0, 0, 0.23) 0px 6px 6px;
}
.signup-alreadyAccount{
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 14px;
    color: rgb(0, 0, 0);
    opacity: 0.8;
    margin-top: 15px;
    font-family: var(--font-alt);
}
.signup-alreadyAccount #signup-loginPage{
    font-size: 17px;
    color: rgb(29, 29, 164);
    font-family: var(--font-alt);
    font-weight: 600;
    padding-left: 10px;
    opacity: 0.9;
}
tr{
    text-align: left;
    font-size: 15px;
    font-weight: 700;
    color: rgb(0, 0, 0);
    opacity: 0.;
    font-family: var(--font-alt);
}
tr,td{
    padding: 0 10px;
}
table{
    border-collapse: separate;
}
td{
    width: 300px;
    padding-top: 0.5em;
    padding-bottom: 0.5em;
}


@media screen and (max-width: 450px){
    #circle{
        display: none;
    }
    .signup-wrapper{
        padding: 55px 10px;
        width: 100%;
        border-radius: 15px;
    }
}