.home-accounts{
    width: 100%;
    background-color: var(--background2);
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    margin: 190px 0;
    font-family:  var(--font-alt);
}
.home-following{
    width: 90%;
    display: flex;
    margin: 0 auto;
    padding: 40px 0;
    justify-content: center;
    align-items: center;
}
.home-icon{
    display: flex;
    font-size: 65px;
    margin: 0 40px;
    gap: 45px;
    justify-content: center;
    align-items: center;
    color: var(--text);
}
.forLine{
    margin: 0 40px;
    width: 1px;
    height: 140px;
    opacity: 0.5;
    background-color: var(--button_text);
}
.social-icon{
    font-size: 80px;
    color: var(--button_text);
}
.social-icon:hover{
    transition: 0.2s ease-in-out;
    scale: 1.1;
}
#insta:hover{
    transition: 1s;
    color: rgb(253, 129, 76);
}
#yt:hover{
    transition: 1s;
    color: rgb(254, 88, 88);
}
#fb:hover{
    transition: 1s;
    color: rgb(154, 194, 243);
}
.social-count{
    /* border: 2px solid yellow; */
    padding: 20px 20px;
    width: 240px;
}
/* .home-youtube div p{
    animation: ytAnimation 5s infinite;
} */
#yt{
    animation: ytAnimation 5s infinite;
}
#fb{
    animation: fbAnimation 5s infinite;
}
#insta{
    animation: instaAnimation 5s infinite;
}
@keyframes ytAnimation {
    50%{
        color: rgb(252, 90, 90);
    }
}
@keyframes fbAnimation {
    50%{
        color: rgb(154, 194, 243);
    }
}
@keyframes instaAnimation {
    50%{
        color: rgb(255, 213, 123);
    }
}


@media screen and (max-width: 450px) {
    .home-accounts{
        width: 100vw;
        margin: 50px 0;
        margin-top: 0;
    }
    .home-following{
        display: flex;
        flex-direction: column;
        padding: 10px 0;
        padding-left: 20%;
    }
    .home-icon{
        font-size: 40px;
        margin: 0 10px;
        gap: 15px;
    }
    .forLine{
        display: none;
    }
    .social-icon{
        font-size: 65px;
    }
    .social-icon:hover{
        scale: 1;
    }
    #insta:hover{
        transition: 1s;
    }
    #yt:hover{
        transition: 1s;
    }
    #fb:hover{
        transition: 1s;
    }
    .social-count{
        padding: 8px 20px;
    }
}