.cartitem-main{
    width: 100%;
    margin: 38px 0;
    box-shadow: rgba(50, 50, 93, 0.15) 0px 6px 12px -2px, rgba(0, 0, 0, 0.2) 0px 3px 7px -3px;
}
.cartitem-wrapper{
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 2rem;
    padding: 20px;
    border-radius: 15px;
    background-color: var(--background2);
    box-shadow: rgba(0, 0, 0, 0.25) 0px 14px 28px, rgba(0, 0, 0, 0.22) 0px 10px 10px;
}
.cartitem-img{
    width: 200px;
    margin-right: 50px;
    margin-left: 15px;
    border-radius: 15px;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
}
.cartitem-details{
    display: flex;
    flex-direction: column;
    gap: 8px;
}
.cartitem-title{
    color: var(--text);
    opacity: 0.8;
    font-family: var(--font-alt);
    font-weight: 700;
    font-size: 35px;
    text-align: left;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
.cartitem-options{
    text-align: center;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 16px;
}
.cartitem-price{
    color: rgb(7, 192, 7);
    font-size: 22px;
    font-weight: 600;
}
.cartitem-button{
    font-size: 30px;
    cursor: pointer;
    color: var(--button_text);
}


@media screen and (max-width: 450px){
    .cartitem-main{
        margin: 15px 0;
    }
    .cartitem-wrapper{
        width: 250px;
        gap: 2rem;
        padding: 15px;
    }
    .cartitem-title{
        font-size: 15px;
        text-align: left;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
    }
    .cartitem-price{
        font-size: 15px;
    }
    .cartitem-button{
        font-size: 20px;
    }
    .cartitem-options{
        gap: 10px;
    }
}