.profileupdate-main{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    position: relative;
}
.profileupdate-wrapper{
    width: 50%;
    margin: 0 auto;
    /* border: 3px solid black; */
    padding: 15px 60px;
    gap: 15px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    /* backdrop-filter: blur(1px); */
    margin: 3rem 0;
    box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 20px, rgba(0, 0, 0, 0.23) 0px 6px 6px;
    background-color: var(--background2);
}
.profileupdate-profilepic img{
    width: 140px;
    height: 140px;
    margin-top: 10px;
    object-fit:cover;
    border-radius: 50%;
    border: 3px solid rgba(197, 196, 196,0.2);
    margin-bottom: 15px;
}
.profileupdate-logo{
    width: 80%;
    margin: 0 auto;
    /* border: 2px solid red; */
}
.profileupdate-profilepic{
    width: 80%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 5px;
    margin: 0 auto;
}
input[type="file"]
{
    margin-left: 10rem;
    color: var(--text);
    opacity: 0.9;
    cursor: pointer;
    font-family: var(--font-alt);
    font-weight: 600;
}
table tbody tr{
    text-align: left;
    vertical-align: center;
}
.profileupdate-info table tbody textarea{
    outline: none;
    border: none;
    padding: 15px 15px;
    border-radius: 15px;
}
input[type="text"]
{
    width: 500px;
    padding: 20px;
    max-width: 100%;
    line-height: 1.5;
    border-radius: 5px;
    box-shadow: 1px 1px 1px #999;
    border: none;
    outline: none;
    opacity: 0.6;
    background-color: white;
}
label{
    font-size: 20px;
    font-weight: 700;
    color: var(--text);
    opacity: 0.85;
    font-family: var(--font-alt);
}
textarea{
    /* box-shadow: 1px 1px 1px #999; */
    width: 500px;
    padding: 20px;
    max-width: 100%;
    line-height: 1.5;
    border-radius: 5px;
    box-shadow: 1px 1px 1px #999;
    border: none;
    outline: none;
    opacity: 0.6;
    box-shadow: rgba(0, 0, 0, 0.74) 0px 3px 8px;
}
.profileupdate-icons{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    gap: 150px;
    margin-top: 1rem;
    color: var(--text);
    font-family: var(--font-alt);
    font-size: 24px;
    font-weight: 600;
}
.profileupdate-icons-confirm{
    display: flex;
    align-items: center;
    gap: 15px;
    cursor: pointer;
    /* border: 2px solid green; */
    padding: 8px 18px;
    box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 20px, rgba(0, 0, 0, 0.23) 0px 6px 6px;
    border-radius: 15px;
}
.profileupdate-icons-cancel{
    border-radius: 15px;
    display: flex;
    align-items: center;
    gap: 15px;
    cursor: pointer;
    padding: 8px 18px;
    box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 20px, rgba(0, 0, 0, 0.23) 0px 6px 6px;
}
.profileupdate-correction{
    color: rgb(18, 220, 18);
    font-size: 30px;
    /* transition: 0.4s ease; */
}
.profileupdate-cancellation{
    color: rgb(251, 57, 57);
    font-size: 30px;
    /* transition: 0.4s ease; */
}


@media screen and (max-width: 450px){
    .profileupdate-wrapper{
        width: 80%;
        padding: 10px 20px;
        gap: 10px;
        margin: 1rem 0;
        padding-bottom: 30px;
    }
    .profileupdate-profilepic img{
        width: 100px;
        height: 100px;
    }
    .profileupdate-info table tbody textarea{
        padding: 5px 5px;
        border-radius: 15px;
    }
    input[type="text"]
    {
        width: 200px;
        padding: 10px;
    }
    textarea{
        /* box-shadow: 1px 1px 1px #999; */
        width: 200px;
        padding: 5px;
    }
    .profileupdate-icons{
        gap: 70px;
        font-size: 14px;
    }
}