.classes{
    width: 100%;
    display: flex;
    flex-direction: column;
    padding: 50px 40px;
    background-color: var(--background);
}
.classes-wrapper{
    width: 100%;
    padding: 40px 120px;
    background: url('../../assets/bg/pencil.jpeg');
    background-position:center;
    background-repeat: no-repeat;
    background-size: cover;
    border-radius: 20px;
    display: flex;
    flex-direction: column;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
}

.classes-menu{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
}
.classes-menu-item{
    font-size: 40px;
    color: rgb(255, 255, 255);
    opacity: 0.8;
    padding: 20px 50px;
    border-radius: 20px;
    letter-spacing: 1px;
    font-family: var(--font-alt);
    font-weight: 600;
    backdrop-filter: blur(3px);
    transition: 0.7s ease;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
}
.classes-menu-item:hover{
    backdrop-filter: blur(5px);
    color: rgb(253, 213, 91);
    opacity: 0.8;
}
.classes-testinomial{
    margin: 2rem 0;
    width: 100%;
    padding-bottom: 2rem;
    border-bottom: 2px solid var(--background2);
}
.classes-testinomial h1{
    font-size: 55px;
    text-align: center;
    margin-top: 4rem;
    font-family: var(--font-base);
    font-weight: 700;
    color: var(--color-golden2);
}
.classes-imageDrawings{
    font-size: 55px;
    margin-top: 1rem;
    font-family: var(--font-base);
    font-weight: 700;
    color: var(--color-golden2);
    position: relative;
}
.classes-imageDrawings h1{
    text-align: center;
    margin-top: 2rem;
}
#classes-fly3{
    position: absolute;
    width: 22px;
    top: 15px;
    left: 470px;
    animation: classes-fly3  4s infinite;
}
@keyframes classes-fly3 {
    50%{
        transform: scale(1.05) translate(2px,4px) rotate(10deg);
    }
}
.classes-workshop{
    margin-top: 4rem;
    padding: 2rem 2rem;
    /* border: 2px solid yellow; */
    display: flex;
    flex-direction: column;
    gap: 3rem;
}
.classes-workshop .classes-block{
    /* border: 2px solid cyan; */
    padding: 0rem 2rem;
}
.classes-workshop .classes-block h1{
    font-size: 45px;
    margin-top: 1rem;
    font-family: var(--font-base);
    font-weight: 700;
    color: var(--text_hover);
    opacity: 0.7;
    text-align: center;
}
.classes-workshop .classes-block p{
    font-family: var(--font-alt);
    color: var(--text);
    opacity: 0.7;
    width: 700;
    font-size: 20px;
    margin-top: 1rem;
}
.classes-workshop .classes-block{
    padding-bottom: 3rem;
    border-bottom: 2px solid var(--background2);
}


/* about course */
.classes-workshopAim{
    width: 100%;
    /* border: 2px solid yellow; */
    margin: 3rem 0;
    margin-top: 5rem;
    display: flex;
    flex-direction: row;
    justify-content: center;
    /* height: 600px; */
    box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px, rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
}
.classes-workshopAim .classes-left{
    position: relative;
    width: 40%;
    /* border: 2px solid cyan; */
    margin: 2rem 0;
}
.classes-workshopAim .classes-left #classes-left-logo
{
    position: absolute;
    width: 100%;
    z-index: 10;
    top: 30px;
    left: 30px;
    border-bottom-left-radius:55%;
    border-bottom-right-radius:55%;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 20px 25px -5px, rgba(0, 0, 0, 0.04) 0px 10px 10px -5px;
}
.classes-workshopAim .classes-left #classes-left-blob
{
    position: absolute;
    width: 250px;
    height: 280px;
    z-index: 5;
    top: 75px;
    left: 215px;
    opacity: 0.5;
    transform: rotate(25deg);
    animation: classes-blob 15s infinite;
}
@keyframes classes-blob {
    50%{
        transform: scale(1.03) translate(-20px,-20px);
    }
}
.classes-workshopAim .classes-left #classes-left-star
{
    position: absolute;
    font-size: 35px;
    top: 50px;
    left: 420px;
    z-index: 15;
    color: var(--color-golden2);
    animation: classes-star 4s infinite;
}
@keyframes classes-star {
    50%{
        transform: scale(1.1);
    }
}


.classes-workshopAim .classes-right{
    position: relative;
    width: 50%;
    /* border: 2px solid cyan; */
    margin: 2rem 2rem;
    padding: 2rem 2rem;
    padding-left: 3rem;
}
.classes-right h1{
    font-size: 60px;
    font-family: var(--font-base);
    font-weight: 600;
    color: var(--color-golden2);
}
.classes-right-list{
    width: 100%;
    /* border: 2px solid green; */
    padding: 5px 35px;
}
.classes-right-list-item{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    gap: 1rem;
    font-size: 21px;
    color: var(--text);
    opacity: 0.75;
    font-family: var(--font-base);
    font-weight: 700;
}
li .classes-pin{
    color: var(--blue);
    border-radius: 50%;
    font-size: 22px;
    font-weight: 500;
    opacity: 0.7;
}
.classes-features{
    display: flex;
    flex-direction: column;
    /* gap: 5px; */
    margin-top: 2rem;
    padding-top: 2rem;
    padding-bottom: 2rem;
    border-top: 2px solid var(--background2);
    border-bottom: 2px solid var(--background2);
}
.classes-features h1{
    font-size: 45px;
    margin-top: 1rem;
    margin-bottom: 1rem;
    font-family: var(--font-base);
    font-weight: 700;
    color: var(--text_hover);
    opacity: 0.7;
    text-align: center;
}
.classes-ongoing-images{
    display: flex;
    flex-direction: row;
    gap: 20px;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    margin-top: 2rem;
}
.classes-ongoing-images img{
    width: 300px;
    border-radius: 20px;
    transition: 0.6s ease;
    box-shadow: rgba(50, 50, 93, 0.55) 0px 13px 27px -5px, rgba(0, 0, 0, 0.5) 0px 8px 16px -8px;
}
.classes-ongoing-images img:hover{
    scale: 1.05;
}

.classes-checkout{
    width: 100%;
    margin-top: 2rem;
    padding: 2rem 4rem;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    gap: 3rem;
}
.classes-checkout h1{
    font-size: 2rem;
    font-family: var(--font-base);
    color: var(--color-golden2);
    font-weight: 700;
    margin-right: 4rem;
}
.classes-checkout .classes-icon{
    font-size: 3rem;
    margin: 0 7px;
}

#classes-insta{
    color: rgb(255, 124, 76);
    transition: ease;
    animation: iconAnimationClassesInsta 5s infinite;
}
@keyframes iconAnimationClassesInsta {
    0%{
        color: rgb(255, 124, 76);
    }
    50%{
        color: palevioletred;
    }
    100%{
        color: rgb(255, 107, 107);
    }
}
#classes-fb{
    color: rgb(16, 178, 219);
    transition: ease;
    animation: iconAnimationClassesfb 5s infinite;
}
@keyframes iconAnimationClassesfb {
    0%{
        color: rgb(16, 178, 219);
    }
    50%{
        color: rgb(184, 193, 230);
    }
    100%{
        color: rgb(84, 154, 234);
    }
}
#classes-yt{
    color: rgb(254, 62, 62);
    transition: ease;
    animation: iconAnimationClassesyt 5s infinite;
}
@keyframes iconAnimationClassesyt {
    0%{
        color: rgb(254, 62, 62);
    }
    50%{
        color: rgb(249, 136, 136);
    }
    100%{
        color: rgb(254, 62, 62);
    }
}
#classes-wp{
    color: rgb(18, 140, 16);
    transition: ease;
    animation: iconAnimationClasseswp 5s infinite;
}
@keyframes iconAnimationClasseswp {
    0%{
        color: rgb(18, 140, 16);
    }
    50%{
        color: rgb(39, 255, 28);
    }
    100%{
        color: rgb(18, 140, 16);
    }
}



@media screen and (max-width: 450px){
    .classes{
        padding: 10px 12px;
    }
    .classes-wrapper{
        padding: 10px 10px;
    }
    .classes-menu-item{
        font-size: 12px;
        padding: 12px 24px;
    }
    .classes-testinomial{
        margin-top: 1rem;
    }
    .classes-testinomial h1{
        font-size: 23px;
        margin-top: 2rem;
    }
    .classes-imageDrawings{
        font-size: 30px;
    }
    #classes-fly3{
        display: none;
    }
    .classes-workshop{
        margin-top: 1rem;
        padding: 1rem 2rem;
        gap: 2rem;
    }
    .classes-workshop .classes-block h1{
        font-size: 25px;
    }
    .classes-workshopAim{
        margin: 1rem 0;
        margin-top: 1rem;
        display: flex;
        flex-direction: column;
    }
    .classes-workshopAim .classes-left{
        width: 100%;
        /* border: 2px solid cyan; */
        margin: 1rem 0;
    }
    .classes-workshopAim .classes-left #classes-left-logo
    {
        display: none;
    }
    .classes-workshopAim .classes-left #classes-left-blob
    {
        display: none;
    }
    .classes-workshopAim .classes-left #classes-left-star
    {
        display: none;
    }
    .classes-workshopAim .classes-right{
        width: 100%;
        margin: 1rem 1rem;
        padding: 1rem 1rem;
    }
    .classes-right h1{
        font-size: 30px;
    }
    .classes-right-list{
        width: 100%;
        padding: 15px 15px;
    }
    .classes-features h1{
        font-size: 35px;
    }
    .classes-ongoing-images{
        display: grid;
        grid-template-columns: repeat(2,1fr);
        gap: 10px;
        margin-top: 1rem;
    }
    .classes-ongoing-images img{
        width: 400px;
    }
    #classes-past{
        width: 100%;
        /* border: 2px solid red; */
    }
    .classes-checkout{
        width: 100%;
        margin-top: 1rem;
        padding: 1rem 1rem;
        gap: 1rem;
    }
    .classes-checkout h1{
        font-size: 22px;
        font-weight: 700;
        margin-right: 1rem;
    }
    .classes-checkout .classes-icon{
        font-size: 22px;
        margin: 0 2px;
    }
}