.product-main{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content:space-between;
    transition: 0.3s ease-in;
    border-radius: 0.75rem;
    gap: 10px;
    margin-top: 50px;
    margin-left: 20px;
    margin-right: 20px;
    padding: 20px;
    border: 2px solid rgb(0, 0, 0,0.15);
}
.product-main:hover{
    scale: 1.05;
    box-shadow: rgba(50, 50, 93, 0.45) 0px 6px 12px -2px, rgba(0, 0, 0, 0.5) 0px 3px 7px -3px;
}
.product-title{
    text-align: center;
    opacity: 0.8;
    color: var(--text);
    font-weight: 600;
    font-size: 1.125rem;
    line-height: 28px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    width: 160px;
    margin-top: 4px;
}
.product-image{
    width: 250px;
    height: 310px;
    border-radius: 15px;
    box-shadow: rgba(60, 64, 67, 0.8) 0px 1px 2px 0px, rgba(60, 64, 67, 0.85) 0px 1px 3px 1px;
}
.product-image img{
    height: 100%;
    width: 100%;
    border-radius: 15px;
}
.product-options{
    display: flex;
    flex-direction: row;
    width: 100%;
    justify-content: space-between;
    align-items: center;
    margin-top: 13px;
}
.product-price{
    color: rgb(26, 243, 26);
    font-size: 22px;
    font-weight: 600;
}
.product-button{
    background-color: white;
    color: rgb(55, 65, 81);
    border-width: 2px;
    border-color: rgb(55,65,81);
    border-radius: 9999px;
    font-weight: 600;
    font-size: 15px;
    padding: 4px;
    padding-left: 15px;
    padding-right: 15px;
    text-transform: uppercase;
    transition: 0.3s ease-in;
}
.product-button:hover{
    background-color: rgb(55, 65, 81);
    color: white;

}

@media screen and (max-width: 450px){
    .product-main{
       gap: 5px;
       margin-top: 10px;
       margin-left: 10px;
       margin-right: 10px;
       padding: 10px; 
    }
    .product-title{
        font-size: 1rem;
        line-height: 28px;
        width: 180px;
        margin-top: 2px;
    }
    .product-image{
        width: 100%;
        height: 100%;
    }
    .product-price{
        font-size: 20px;
    }
    .product-button{
        font-size: 15px;
        padding: 5px;
        padding-left: 15px;
        padding-right: 15px;
    }
}