.faq-main{
    width: 100%;
    margin-top: 2rem;
    /* border: 2px solid yellow; */
}
.faq-wrapper{
    background-color: var(--background2);
    width: 90%;
    padding: 2rem 5rem;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    /* align-items: center; */
    box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px, rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
}
.faq-wrapper h1{
    font-size: 45px;
    margin-bottom: 1rem;
    font-family: var(--font-base);
    font-weight: 700;
    color: var(--text_hover);
    opacity: 0.7;
    text-align: center;
}
.faq-item{
    margin: 1rem 1rem;
    float: left;
    padding-bottom: 2rem;
    border-bottom: 2px solid var(--background);
    transition: 0.4s ease;
}
.faq-item .faq-answer{
    max-height: 0;
    display: none;
    font-size: 15px;
    color: var(--text);
    opacity: 0.7;
    padding: 10px 0;
    font-weight: 500;
    text-align: center;
    transition: max-height 0.5s ease;
    font-family: var(--font-alt);
    float: left;
    padding-left: 5rem;
    transition: 0.4s ease;
}
.faq-item .faq-question{
    font-size: 20px;
    cursor: pointer;
    color: var(--blue);
    opacity: 0.75;
    font-family: var(--font-alt);
    font-weight: 700;
    display: flex;
    gap: 2rem;
    align-items: center;
    justify-content: flex-start;
    transition: 0.4s ease;
}
.faq-item.faqActive .faq-answer{
    max-height: 500px;
    display: block;
    transition: 1s ease;
    animation: fade 0.4s ease-in-out;
}
.faq-item.faqActive .faq-question .faq-quest-logo{
    transform: rotate(90deg);
    transition: 0.4s ease;
}
@keyframes fade {
    from{
        opacity: 0;
        transform: translateY(-10px);
    }
    to{
        opacity: 1;
        transform: translateY(0px);
    }
    
}


@media screen and (max-width: 450px){
    .faq-main{
        margin-top: 1rem;
    }
    .faq-wrapper{
        width: 100%;
        padding: 1rem 2rem;
    }
    .faq-wrapper h1{
        font-size: 26px;
    }
    .faq-item .faq-question{
        font-size: 15px;
        gap: 1rem;
    }
}