.cart-wrapper{
    width: 100%;
    display: flex;
}
.cart-items{
    width: 40%;
    margin: 0 auto;
}
.cart-info{
    width: 40%;
    margin: 0 auto;
}
.cart-summary{
    display: flex;
    flex-direction: column;
    gap: 20px;
    margin: 20px;
    padding: 20px;
    margin-top: 64px;
    justify-content: center;
    align-items: center;
}
.cart-heading{
    font-weight: bold;
    opacity: 0.8;
    font-size: 68px;
    color: var(--text);
    font-family: var(--font-base);
}   
.cart-para{
    font-size: 30px;
    font-weight: 600;
    text-transform: capitalize;
    opacity: 0.7;
    color: var(--text);
}
.cart-total{
    font-weight: 600;
    font-size: 22px;
    color: var(--text);
    opacity: 0.9;
}
.cart-totalitems{
    color: var(--blue);
    margin-left: 8px;
    font-size: 24px;
    opacity: 0.8;
}
.cart-final{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 32px;
    padding: 20px;
    margin: 20px;
    margin-top: 100px;
}
.cart-totalamount{
    font-size: 30px;
    font-weight: 600;
    color: var(--text);
    opacity: 0.8;
}
.cart-rupee{
    color: rgb(19, 213, 90);
    margin-left: 12px;
    font-size: 38px;
}
.cart-button{
    color: var(--text);
    background-color: var(--background2);
    border-color: rgb(117, 131, 154);
    border-radius: 15px;
    font-weight: 600;
    font-size: 20px;
    padding: 13px 24px;
    text-transform: uppercase;
    transition: 0.4s ease;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px, rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
}
.cart-button:hover{
    background-color: rgb(21, 128, 61);
    color: white;
}
.cart-empty{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 70px;
    padding: 40px;
}
.cart-empty-heading{
    font-size: 68px;
    font-family: var(--font-base);
    font-weight: 600;
    color: var(--color-golden2);
    opacity: 0.9;
    margin: 20px;
    margin-bottom: 64px;
}



@media screen and (max-width: 450px){
    .cart-wrapper{
        gap: 20%;
    }
    .cart-summary{
        gap: 10px;
        margin-top: 20px;
        padding: 15px;
    }
    .cart-heading{
        font-size: 20px;
    }
    .cart-para{
        font-size: 20px;
    }
    .cart-total{
        font-size: 15px;
    }
    .cart-totalitems{
        margin-right: 20px;
        font-size: 25px;
    }
    .cart-final{
        gap: 12px;
        padding: 10px;
        margin: 10px;
        margin-top: 30px;
    }
    .cart-totalamount{
        font-size: 19px;
    }
    .cart-rupee{
        margin-left: 20px;
        font-size: 28px;
    }
    .cart-button{
        font-size: 14px;
        padding: 10px 15px;
    }
}