.paintings{
    width: 100%;
    display: flex;
    flex-direction: column;
    padding: 50px 40px;
    background-color: var(--background);
}
.paintings-wrapper{
    width: 100%;
    padding: 40px 40px;
    background: url('../../assets/bg/sketch.jpeg');
    background-position:center;
    background-repeat: no-repeat;
    background-size: cover;
    border-radius: 20px;
    display: flex;
    flex-direction: column;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
    margin-bottom: 2rem;
}
.paintings-menu{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    position: relative;
    justify-content: space-between;
}
.paintings-menu-item{
    font-size: 40px;
    color: black;
    opacity: 0.8;
    padding: 20px 50px;
    border-radius: 20px;
    letter-spacing: 1px;
    font-family: var(--font-alt);
    font-weight: 600;
    backdrop-filter: blur(3px);
    transition: 0.7s ease;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
}
.paintings-menu-item:hover{
    backdrop-filter: blur(5px);
    color: rgb(25, 83, 124);
    opacity: 0.8;
}
.paintings-block{
    margin: 3rem 0;
    box-shadow: rgba(50, 50, 93, 0.35) 0px 13px 27px -5px, rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
    border-radius: 15px;
    position: relative;
}
.paintings-block #paintings-fly3{
    position: absolute;
    width: 22px;
    top: 30px;
    left: 650px;
    animation: paintings-fly3  4s infinite;
}
@keyframes paintings-fly3 {
    50%{
        transform: scale(1.05) translate(2px,4px) rotate(10deg);
    }
}
.paintings-gallery-content{
    padding: 0 35px;
    padding-top: 15px;
}
.paintings-checkout{
    width: 100%;
    margin-top: 2rem;
    padding: 2rem 4rem;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    gap: 3rem;
}
.paintings-checkout h1{
    font-size: 2rem;
    font-family: var(--font-base);
    color: var(--color-golden2);
    font-weight: 700;
    margin-right: 4rem;
}
.paintings-checkout .paintings-icon{
    font-size: 3rem;
    margin: 0 7px;
}

#paintings-insta{
    color: rgb(255, 124, 76);
    transition: ease;
    animation: iconAnimationPaintingsInsta 5s infinite;
}
@keyframes iconAnimationPaintingsInsta {
    0%{
        color: rgb(255, 124, 76);
    }
    50%{
        color: palevioletred;
    }
    100%{
        color: rgb(255, 107, 107);
    }
}
#paintings-fb{
    color: rgb(16, 178, 219);
    transition: ease;
    animation: iconAnimationPaintingsfb 5s infinite;
}
@keyframes iconAnimationPaintingsfb {
    0%{
        color: rgb(16, 178, 219);
    }
    50%{
        color: rgb(184, 193, 230);
    }
    100%{
        color: rgb(84, 154, 234);
    }
}
#paintings-yt{
    color: rgb(254, 62, 62);
    transition: ease;
    animation: iconAnimationPaintingsyt 5s infinite;
}
@keyframes iconAnimationPaintingsyt {
    0%{
        color: rgb(254, 62, 62);
    }
    50%{
        color: rgb(249, 136, 136);
    }
    100%{
        color: rgb(254, 62, 62);
    }
}


@media screen and (max-width: 450px){
    .paintings{
        padding: 10px 12px;
    }
    .paintings-wrapper{
        padding: 10px 12px;
    }
    .paintings-menu{
        width: 100%;
    }
    .paintings-menu-item{
        font-size: 10px;
        padding: 10px 10px;
    }
    .paintings-block{
        margin: 1rem 0;
    }
    .paintings-block #paintings-fly3{
        display: none;
    }
    .paintings-gallery-content{
        padding: 0 1px;
        padding-top: 1px;
    }
    .paintings-checkout{
        margin-top: 1rem;
        padding: 1rem 1rem;
        gap: 1rem;
    }
    .paintings-checkout h1{
        font-size: 18px;
        margin-right: 2rem;
    }
    .paintings-checkout .paintings-icon{
        font-size: 2rem;
        margin: 0 2px;
    }
}