.hero-section{
    margin: 100px 0px;
    width: 100%;
    display: flex;
    gap: 4%;
}
.hero-section .right #rightHero{
    width: 450px;
    z-index: 2;
    border-radius: 42%;
    /* box-shadow: rgb(38, 57, 77) 0px 20px 30px -10px; */
    /* box-shadow: rgba(0, 0, 0, 0.25) 0px 25px 50px -12px; */
    box-shadow: rgba(0, 0, 0, 0.3) 0px 19px 38px, rgba(0, 0, 0, 0.22) 0px 15px 12px;
}
.hero-section .right{
    margin-right: 8%;
    width: 40%;
    display: flex;
    flex-direction: column;
    /* border: 2px solid rgb(20, 14, 216); */
    position: relative;
    justify-content: center;
    align-items: center;
}
.right #blob{
    position: absolute;
    width: 300px;
    z-index: 1;
    bottom: 10px;
    left: 132px;
    animation: blob 10s infinite;
}
@keyframes blob {
    50%{
        transform: translate(30px,-30px);
    }
}
.right #butterfly{
    position: absolute;
    width: 30px;
    z-index: 3;
    top: 120px;
    right: 140px;
    animation: fly 10s infinite;
}
@keyframes fly {
    50%{
        top: 60px;
        right: 160px;
    }
}
.hero-section .left{
    width: 40%;
    display: flex;
    flex-direction: column;
    /* border: 2px solid red; */
    margin-left: 8%;
    justify-content: center;
    align-items: center;
    line-height: 50px;
}
.left #leftWriter{
    display: flex;
    gap: 10px;
}
.left #leftAbout{
    font-size: 50px;
    font-weight: bold;
    letter-spacing: 3px;
    color: var(--text);
}
.left #leftAbout span{
    font-size: 80px;
    padding-right: 4px;
    color: rgb(255, 117, 117);
}
.left #leftName{
    font-size: 40px;
    font-weight: 700;
    font-style: italic;
    margin: 30px 0px;
    color: var(--button_text);
}
.left #leftName span{
    font-size: 60px;
    font-family:cursive;
    color: rgb(113, 139, 255);
}
#leftWriter p{
    font-size: xx-large;
    font-weight: 600;
    font-family:Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    color: var(--text);
}
#leftWriter span{
    font-size: 30px;
    margin-left: 5px;
    font-weight: 400;
    font-family: cursive;
    opacity: 0.95;
    color: var(--text_hover);
}
#intro{
    font-family: var(--font-base);
    color: var(--text);
    font-size: 15px;
    margin-top: 2rem;
    text-transform: capitalize;
    font-weight: 600;
}
#intro span{
    color: var(--blue);
    font-size: 15.1px;
    margin: 0px 5px;
    font-weight: 800;
}


@media screen and (max-width: 450px) {
    .hero-section{
        display: flex;
        flex-direction: column;
        margin-top: 50px;
        width: 100vw;
        gap: 50px;
    }
    .hero-section .left{
        width: 80%;
        margin: 0 auto;
        line-height: 30px;
    }
    .hero-section .right{
        width: 80%;
        margin: 0 auto;
    }
    .hero-section .right #rightHero{
        margin: 0 auto;
    }
    .right #blob{
        position: absolute;
        width: 220px;
        z-index: 1;
        bottom: 1%;
        left: 15%;
        transform: rotate(50deg);
        animation: blob 10s infinite;
    }
    @keyframes blob {
        50%{
            transform: translate(20px,0px);
        }
    }
    .right #butterfly{
        position: absolute;
        width: 16px;
        z-index: 3;
        top: 1%;
        right: 1%;
        animation: fly 10s infinite;
    }
    @keyframes fly {
        50%{
            top: 20px;
            right: 20px;
        }
    }
    
    .left #leftName{
        font-size: 30px;
    }
    .left #leftName span{
        font-size: 50px;
    }
    #leftWriter span{
        font-size: 20px;
    }
}