.forget-main{
    /* border: 2px solid yellow; */
    width: 100%;
}
.forget-wrapper{
    width: 60%;
    height: 80vh;
    /* border: 2px solid greenyellow; */
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items:  center;
    margin: 0 auto;
    gap: 20px;
}
.forget-email{
    /* border: 2px solid red; */
    padding: 0 20px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 30px;
}
.forget-email input[type="text"]{
    width: 50%;
}
.forget-email label{
    color: var(--text);
    opacity: 0.87;
    font-family: var(--font-alt);
}
#forget-button{
    /* border: 2px solid green; */
    margin-top: 20px;
    color: var(--button_text);
    font-family: var(--font-alt);
    font-size: 19px;
    font-weight: 600;
    padding: 8px 16px;
    border-radius: 20px;
    background-color: var(--background2);
    transition: 0.5s ease;
    box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 20px, rgba(0, 0, 0, 0.23) 0px 6px 6px;
}
#forget-button:hover{
    color: rgb(9, 201, 9);
}