.leftslide-gallery{
    display: flex;
    flex-direction: row;
    background: var(--background2);
    padding: 2rem 2rem 2rem 2rem;
    margin-top: 1rem;
    justify-content: center;
    align-items: center;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    width: 100%;
}
.leftslide-image{
    display: flex;
    flex-direction: row;
    max-width: 100%;
    position: relative;
}
.leftslide-image_container{
    display: flex;
    flex-direction: row;
    width: max-content;
    overflow-x: scroll;
    -ms-overflow-style: none;
    scrollbar-width: none;  
}
.leftslide-image_container::-webkit-scrollbar {
    display: none;
}
.leftslide-card{
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    min-width: 301px;
    height: 447px;
    margin-right: 2rem;
    border-radius: 25px;
    transition: 0.5s ease;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}
.leftslide-image-icon{
    position: absolute;
    color: #fff;
    font-size: 2rem;
    opacity: 0;
    transition: 0.7s ease;
    cursor: pointer;
}
.leftslide-card img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    opacity: 0.9;
    transition: 0.7s ease;
    border-radius: 25px;
}
.leftslide-card:hover img {
    opacity: 1;
    scale: 1.02;
}
.leftslide-arrows{
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 1rem;
    position: absolute;
    bottom: 5%;
}
.leftslide-arrow-icon {
    color: var(--color-golden);
    font-size: 2rem;
    cursor: pointer;
    background: var(--color-black);
    border-radius: 5px;
}
.leftslide-arrow-icon:hover {
    color: var(--color-white);
}


@media screen and (max-width: 450px){
    .leftslide-gallery{
        padding: 1rem 1rem;
    }
    .leftslide-card{
        min-width: 201px;
        height: 347px;
        margin-right: 1rem;
        border-radius: 20px;
    }
}