.feature-right-main{
    width: 100%;
    padding: 2rem 2rem;
}
.feature-right-wrapper{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    gap: 4rem;
    width: 70%;
    float: right;
    padding: 2rem 4rem;
    border: 2px solid var(--background2);
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    border-radius: 20px;
}
.feature-right-wrapper .feature-right-rightside img{
    width: 700px;
    margin-right: 4rem;
    border-radius: 20px;
}
.feature-right-wrapper .feature-right-leftside{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    gap: 2rem;    
}
.feature-right-wrapper .feature-right-leftside h1{
    font-size: 50px;
    font-weight: 600;
    color: var(--color-golden2);
    font-family: var(--font-base);
}
.feature-right-wrapper .feature-right-leftside p{
    color: var(--text);
    opacity: 0.7;
    padding: 0px 20px;
    font-weight: 400;
    font-family: var(--font-alt);
}


@media screen and (max-width: 450px){
    .feature-right-main{
        padding: 1rem 1rem;
    }
    .feature-right-wrapper{
        /* border: 2px solid red; */
        gap: 1rem;
        width: 100%;
        float: center;
        flex-direction: column;
        padding: 1rem 1rem;
    }
    .feature-right-wrapper .feature-right-rightside img{
        width: 100%;
        margin-right: 1rem;
        border-radius: 15px;
    }
    .feature-right-wrapper .feature-right-leftside{
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: center;
        gap: 1rem;    
    }
    .feature-right-wrapper .feature-right-leftside h1{
        font-size: 30px;
    }
    .feature-right-wrapper .feature-right-leftside p{
        padding: 0px 10px;
    }
}