.review-main{
    width: 100%;
    margin: 2rem 0;
}
.review-wrapper{
    display: grid;
    grid-template-columns: repeat(4,1fr);
    gap: 50px;
    padding: 20px 60px;
    /* border: 2px solid yellow; */
}
.review-wrapper div{
    height: 100px;
}
.review-wrapper div img{
    border-radius: 5%;
    box-shadow: rgb(38, 57, 77) 0px 20px 30px -10px;
    animation: ReviewAnimation 5s infinite;
}
@keyframes ReviewAnimation {
    50%{
        transform: scale(1.03) translate(1px,1px) rotate(1deg);
    }
}


@media screen and (max-width: 450px){
    .review-main{
        margin: 1rem 0;
    }
    .review-wrapper{
        grid-template-columns: repeat(2,1fr);
        gap: 15px;
        padding: 10px 10px;
        /* border: 2px solid yellow; */
    }
    .review-wrapper div{
        height: 95px;
    }
}