@import url('https://fonts.googleapis.com/css2?family=Mukta&family=Poppins:wght@500&display=swap');
.navbar{
    font-family: 'Poppins', sans-serif;
    width: 100%;
    background-color: var(--bg_black);
    /* height: 90px; */
    box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
}
.wrapper{
    width: 95%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    text-align: center;
    margin: 0 auto;
}
.wrapper .logo{
    width: 100px;
}
.wrapper .navMenu ul{
    display: flex;
    justify-content: space-between;
    align-items: center;
    text-align: center;
    gap: 55px;
    color: var(--text);
    font-size: larger;
    font-weight: 600;
}
.wrapper .navMenu ul li:hover{
    scale: 1.05;
    color: var(--text_hover);
    transition: all 0.5s ease-in-out;
}
.wrapper .sidebar{
    margin-right: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    gap: 22px;
}
.sidebar .light{
    color: var(--light_mode);
    font-size: x-large;
    font-weight: 700;
    cursor: pointer;
    transition: 0.5s ease;
}
.sidebar .light:hover{
    scale: 1.08;
    color: rgb(255, 255, 133);
    
}
.sidebar .dark{
    transition: 0.5s ease;
    color: var(--button_text);
    font-size: x-large;
    cursor: pointer;
    font-weight: 700;
}
.sidebar .dark:hover{
    scale: 1.08;
    color: rgb(118, 135, 169);
}
.sidebar .profile{
    transition: 0.5s ease;
    color: var(--button_text);
    font-size: x-large;
    cursor: pointer;
    font-weight: 700;
}
.sidebar .profile:hover{
    scale: 1.08;
    color: var(--text_hover);
}
.sidebar button{
    margin: 5px 5px;
    padding: 8px 15px;
    font-weight: 700;
    font-size: 16px;
    border-radius: 10%;
    color: var(--button_text);
    box-shadow: rgba(94, 94, 94, 0.24) 0px 3px 8px;
    transition: 0.6s ease-in-out;
}
.sidebar button:hover{
    color: var(--text_hover);
}
.active{
    transition: 0.5s ease-in-out;
    color: var(--text_hover);
}


@media screen and (max-width: 450px) {
    .wrapper{
        padding-top: 2rem;
        flex-direction: column;
        padding-bottom: 1rem;
    }
    .wrapper .logo{
        display: none;
    }
    .wrapper .navMenu ul{
        display: grid;
        grid-template-columns: repeat(4,1fr);
        gap: 20px;
        font-size: large;
    }
    .wrapper .navMenu ul li:hover{
        scale: 1;
    }
    .wrapper .sidebar{
        margin-right: 10px;
        gap: 20px;
    }
    .sidebar .light{
        font-size: large;
    }
    .sidebar .dark{
        font-size: large;
    }
    .sidebar .profile{
        font-size: large;
    }
}