.contact-form{
    width: 100%;
    box-shadow: rgba(112, 76, 76, 0.45) 0px 5px 15px;
    font-family:  var(--font-alt);
    background: url('../../assets/bg/leaves3.jpeg');
    background-repeat: no-repeat;
    position: relative;
    background-size: cover;
}
.contact-jyoti{
    position: absolute;
}
.contact-main{
    width: 700px;
    border-radius: 80%;
    top: 12%;
    left: 3%;
    z-index: 5;
    box-shadow: rgba(0, 0, 0, 0.3) 0px 19px 38px, rgba(0, 0, 0, 0.22) 0px 15px 12px;
}
.contact-blob{
    width: 320px;
    top: 10%;
    left: 18%;
    opacity: 0.5;
    z-index: 3;
    transform: rotateX(30deg);
    animation: blobAnimation 10s infinite;
}
@keyframes blobAnimation {
    50%{
        top: 10%;
        left: 20%;
    }
}
.contact-fly{
    width: 23px;
    top: 15%;
    left: 15%;
    animation: flyAnimation 2s infinite;
}
@keyframes flyAnimation {
    50%{
        top: 19%;
        left: 45%;
    }
}


.contact-wrapper{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 25px 0;
    width: 600px;
}
.contact-intro{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    text-align: center;
    padding: 0px 70px;
}
.form-input{
    display: flex;
    justify-content: flex-start;
    /* border: 2px solid green; */
    padding: 15px 15px;
    gap: 15px;
    align-items: center;
}
.form-comment{
    display: flex;
    flex-direction: column;
    gap: 10px;
}
.form-comment label{
    font-size: 20px;
    font-weight: 700;
    color: whitesmoke;
    opacity: 0.8;
    font-family: var(--font-alt);
}
textarea[placeholder]{
    width: 500px;
    padding: 20px;
    max-width: 100%;
    line-height: 1.5;
    border-radius: 5px;
    box-shadow: 1px 1px 1px #999;
    border: none;
    outline: none;
    opacity: 0.6;
}
.contact-intro h1{
    font-size: 50px;
    font-weight: 500;
    font-family: var(--font-base);
    color: white;
    opacity: 0.7;
}
.contact-intro p{
    margin: 10px 0;
    font-family: var(--font-alt);
    font-size: 18px;
    color: whitesmoke;
    opacity: 0.5;
}
input[type=text], select {
    width: 100%;
    padding: 5px 20px;
    margin: 8px 0;
    display: inline-block;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
    outline: none;
    border: none;
    opacity: 0.6;
    box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
}
.form-input label{
    font-size: 20px;
    font-weight: 700;
    color: whitesmoke;
    opacity: 0.7;
    font-family: var(--font-alt);
}
#button{
    font-size: 25px;
    padding: 4px 20px;
    width: 200px;
    margin-top: 38px;
    border-radius: 15px;
    box-shadow: rgba(0, 0, 0, 1) 0px 5px 15px;
    color: var(--text);
    font-weight: 800;
    letter-spacing: 1px;
    transition: 1s ease;
}
#button:hover{
    background-color: rgb(67, 145, 98);
    cursor: pointer;
}
form{
    float: right;
    margin-right: 100px;
    margin-top: 30px;
    margin-bottom: 55px;
    backdrop-filter: blur(3px);
    box-shadow: rgba(0, 0, 0, 0.3) 0px 19px 38px, rgba(0, 0, 0, 0.82) 0px 15px 12px;
}

@media screen and (max-width: 450px){
    .contact-jyoti{
        display: none;
    }
    .contact-wrapper{
        padding: 20px 0;
        padding-bottom: 50px;
        width: 100%;
    }
    .contact-intro{
        width: 100%;
        padding: 0px 20px;
    }
    .form-input{
        padding: 10px 10px;
        gap: 10px;
    }
    textarea[placeholder]{
        width: 100%;
        padding: 10px;
        max-width: 100%;
    }
    input[type=text], select {
        width: 100%;
        padding: 2px 10px;
        margin: 2px 0;
        box-shadow: rgba(0, 0, 0, 0.05) 1.95px 1.95px 2.6px;
    }
    #button{
        font-size: 20px;
        padding: 5px 20px;
        width: 150px;
        margin-top: 28px;
    }
}