.order-main{
    width: 100%;
    margin-top: 2rem;
    padding-bottom: 4rem;
    position: relative;
    /* border: 2px solid cyan; */
}
.order-wrapper{
    display: grid;
    grid-template-columns: repeat(4,1fr);
    /* max-width: 72rem; */
    padding: 8px 80px;
    margin: 0px auto;
    min-height: 80vh;
    /* border: 2px solid gold; */
}
.order-main h1{
    font-size: 70px;
    font-family: var(--font-base);
    color: var(--color-golden2);
    font-weight: 700;
    text-align: center;
}
.order-fly{
    position: absolute;
    width: 30px;
    top: 15px;
    left: 400px;
    animation: order-fly-animation 4s infinite;
}
@keyframes order-fly-animation {
    50%{
        transform: scale(1.002) translate(5px,-1px);
    }
    
}


@media screen and (max-width: 450px){
    .order-main{
        margin-top: 1rem;
        padding-bottom: 2rem;
    }
    .order-wrapper{
        display: flex;
        flex-wrap: wrap;
        padding: 1px 0px;
    }
    .order-main h1{
        font-size: 34px;
    }
    .order-fly{
        display: none;
    }
}