.profile-main{
    position: relative;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background: url('../../assets/bg/pencil.jpeg');
    background-repeat: no-repeat;
    background-size: cover;
}
.profile-wrapper{
    display: flex;
    /* background-color: var(--background2); */
    backdrop-filter: blur(20px);
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 5% 0;
    padding: 0 50px;
    padding-top: 35px;
    padding-bottom: 20px;
    /* border: 2px solid yellow; */
    gap: 35px;
    box-shadow: rgba(0, 0, 0, 0.34) 0px 3px 8px;
    width: 40%;
}
.profile-info table{
    border-collapse: collapse;
    width: 80%;
    margin: 0 auto;
    /* border: 2px solid red; */
}
.profile-info table tbody tr td{
    color: whitesmoke;
    opacity: 0.9;
    font-weight: 600;
}
.profile-info table tbody tr{
    text-align: left;
    vertical-align: top;
}
.profile-pic img{
    width: 160px;
    height: 160px;
    object-fit:cover;
    border-radius: 50%;
    border: 3px solid rgba(197, 196, 196,0.2);
}
.profile-edit{
    position: absolute;
    display: flex;
    gap: 20px;
    align-items: center;
    top: 3%;
    right: 5%;
    color: var(--color-crimson);
    opacity: 0.9;
    font-size: 20px;
    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
    /* border: 2px solid yellow; */
    padding: 10px 18px;
    transition: 0.5s ease;
    cursor: pointer;
    font-weight: 800px;
    border-radius: 15px;
}
.profile-edit h3{
    font-family: var(--font-alt);
}
.profile-edit .profile-edit-icon:hover{
    scale: 1.05;
    color: rgb(113, 193, 255);
    opacity: 0.8;
}



@media screen and (max-width: 450px){
    .profile-wrapper{
        width: 100%;
    }
    .profile-edit{
        z-index: 100;
        right: 0;
        top: 0;
        font-size: 15px;
        padding: 10px 12px;
    }
}