.login-form{
    width: 100%;
    box-shadow: rgba(112, 76, 76, 0.45) 0px 5px 15px;
    font-family:  var(--font-alt);
    background-repeat: no-repeat;
    position: relative;
    background-size: cover;
    background: url('../../assets/bg/leaves2.jpeg');
    background-repeat: no-repeat;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 90vh;
    background-size: cover;
}

.login-wrapper{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 25px 0;
    width: 600px;
    border-radius: 15px;
}
.login-input{
    display: flex;
    justify-content:center;
    padding: 15px 15px;
    gap: 15px;
    align-items: center;
}

#login-heading{
    font-size: 50px;
    font-weight: 500;
    font-family: var(--font-base);
    color: white;
    opacity: 0.7;
}

input[type=text], select {
    width: 100%;
    padding: 5px 20px;
    margin: 8px 0;
    display: inline-block;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
    outline: none;
    border: none;
    opacity: 0.6;
    box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
}
input[type=password], select {
    width: 100%;
    padding: 5px 20px;
    margin: 8px 0;
    display: inline-block;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
    outline: none;
    border: none;
    opacity: 0.6;
    box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
}
#login-button{
    font-size: 20px;
    padding: 3px 15px;
    width: 150px;
    margin-top: 38px;
    border-radius: 15px;
    box-shadow: rgba(0, 0, 0, 1) 0px 5px 15px;
    color: white;
    font-weight: 600;
    letter-spacing: 1px;
    transition: 1s ease;
}
#login-button:hover{
    background-color: rgb(58, 125, 101);
    cursor: pointer;
}
form{
    margin: 0 auto;
    margin-top: 30px;
    margin-bottom: 55px;
    backdrop-filter: blur(4px);
    box-shadow: rgba(0, 0, 0, 0.89) 0px 10px 20px, rgba(0, 0, 0, 0.83) 0px 6px 6px;
}
#login-forgot{
    display: flex;
    justify-content: space-between;
    gap: 100px;
    align-items: center;
    font-size: 16px;
    color: white;
    opacity: 0.5;
    margin-top: 15px;
}
#create:hover{
    transition: 0.6s ease;
    opacity: 0.9;
    color: rgb(164, 164, 230);
}
#forgot:hover{
    color: rgb(164, 164, 230);
    transition: 0.6s ease;
    opacity: 0.9;
}
th{
    text-align: left;
    font-size: 18px;
    font-weight: 700;
    color: whitesmoke;
    opacity: 0.7;
    font-family: var(--font-alt);
}
th,td{
    padding: 0 10px;
}
table{
    border-collapse: separate;
}
td{
    padding-top: 0.5em;
    padding-bottom: 0.5em;
}

@media screen and (max-width: 450px){
    .login-wrapper{
        width: 100%;
        padding: 55px 10px;
        width: 500px;
    }
}