#wrapper{
    margin: 80px 0;
    width: 100%;
    background-color: var(--background2);
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}
.container{
    width: 100%; 
    margin: 0 auto;
    padding: 100px;
    position: relative;
}
.container #gridFly{
    position: absolute;
    width: 25px;
    left: 25%;
    top: 6%;
    transition: all ease;
    animation: flyAnimation 2s infinite;
}
@keyframes flyAnimation {
    50%{
        transform: translate(-3%,-15%);
    }
}
.container h1{
    margin: 20px 0;
    text-align: center;
    margin-bottom: 60px;
    font-family: var(--font-base);
    color: var(--color-golden2);
    font-size: 64px;
    line-height: 83.2px;
    letter-spacing: 0.04em;
    text-transform: capitalize;
}
.gallery{
    display: grid;
    grid-template-columns: repeat(4,1fr);
}
.card{
    width: 87%;
    position: relative;
    margin-bottom: 20px;
    border-radius: 15px;
    overflow: hidden;
    transition: 0.5s ease;
    box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 20px, rgba(0, 0, 0, 0.23) 0px 6px 6px;
}
.card img{
    width: 100%;
    height: 100%;
    filter: grayscale(100%);
    object-fit: cover;
}
.card:hover{
    transition: 0.5s ease;
    transform: scale(1.03);
    filter: drop-shadow(0 0 5px #333);
}
.card:hover img{
    filter: grayscale(0%);
}
.card figcaption{
    position: absolute;
    bottom: 0;
    left: 0;
    padding: 25px;
    width: 100%;
    height: 20%;
    font-size: 16px;
    font-weight: 500;
    color: white;
    opacity: 0;
    border-radius: 0 0 10px 10px;
    background: linear-gradient(0deg, rgba(0,0,0,0.5) 0%,rhba(255,255,255,0.5) 100%);
    transition: 0.3s;
}
.card:hover figcaption{
    opacity: 1;
    transform: scale(1.03);
}



@media screen and (max-width: 450px) {
    .container h1{
        margin: 10px 0;
        margin-bottom: 20px;
        font-size: 34px;
        line-height: 50.2px;
    }
    .container #gridFly{
        width: 15px;
        left: 3%;
        top: 3%;
    }
    .container{
        padding: 20px;
    }
    #wrapper{
        width: 100vw;
    }
    .gallery{
        display: grid;
        grid-template-columns: repeat(3,1fr);
        grid-template-rows: repeat(4,1fr);
    }
    .gallery figure:first-child{
        display: none;
    }
    .card img{
        filter: grayscale(0%);
    }
    .card:hover{
        transition: 0.5s ease;
        transform: scale(1);
        filter: drop-shadow(0 0 0px #333);
    }
    .card figcaption{
        display: none;
    }
    .card:hover figcaption{
        display: none;
    }
}